<script>
import { __ossMirror } from "@jx3box/jx3box-common/js/jx3box";
// import minirank from './minirank'
import { getEvents, getTeams } from "@/service/next.js";
import {showAvatar} from '@jx3box/jx3box-common/js/utils.js'
export default {
    name: "list_side",
    props: [],
    data: function() {
        return {
            teams : [],
            events : []
        };
    },
    computed: {
        zlp: function() {
            return this.$store.state.zlp;
        },
        fb: function() {
            return this.$store.state.fb;
        },
        info: function() {
            return this.$store.state.map[this.zlp]["dungeon"][this.fb];
        },
        img: function() {
            return __ossMirror + this.info.icon;
        },
        introduce: function() {
            return this.info.detail.introduce.replace(/\\n/g, "<br/>");
        },
    },
    methods: {},
    mounted: function() {
        getEvents().then((res) => {
            this.events = res.data.data.list
        })
        getTeams().then((res) => {
            this.teams = res.data.data.list
        })
    },
    filters : {
        showLogo : function (val){
            return showAvatar(val)
        },
        eventLink : function (val){
            return '/rank/race.html#/' + val
        },
        teamLink : function (val){
            return '/team/#/org/view/' + val
        }
    },
    components: {
        // minirank
    },
};
</script>

